body {
  background: #ECF1F8;
  font-family: "Proxima Nova,sans-serif";
  padding-bottom: 38px;

  &.enlarged {
    min-height: auto;
  }
}

.content-page {
  margin: 80px 13px 0 281px !important;
  padding-top: 25px;
  transition: all .2s ease-out;
  min-height: auto;
}

.enlarged .content-page {
  margin-right: 0 !important;
}

._nav-hidden {
  .left-side-menu {
    transform: translateX(-100%);
  }

  .content-page {
    margin-left: 0 !important;
  }
}

// Tabs
.nav-bordered {
  border-color: transparent !important;

  li a {
    padding: 10px 20px 4px!important;
    border-bottom: 4px solid transparent !important;
    transition: 0.4s;

    &:hover {
      border-bottom-color: #B6FFE8 !important;
      color: #0000FF;
    }

    &.active {
      border-bottom: 4px solid #00eba8 !important;
    }
  }
}

.nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
  background-color: transparent;
  color: #0000FF;
  font-size: 14px;
}

.tab-content {
  padding-top: 31px;
}

// Side menu
.left-side-menu-sm {
  .left-side-menu {
    width: 100px;
    padding: 0;

    #sidebar-menu>ul>li>a>i {
      line-height: 1;
    }
  }

  .logo-box {
    width: 100px;
  }
}

.left-side-menu {
  top: 80px;
}

#sidebar-menu>ul>li>a {
  padding: 13px 0;
  font-family: "Proxima Nova,sans-serif";
  font-weight: 600;
  font-size: 12px;
  color: #212B35;

  svg {
    transition: all .4s;
  }

  &:hover {
    color: #0000FF;

    svg {
      fill: #0000FF;
    }
  }
}

#sidebar-menu>ul>li.active>a {
  color: #0000FF;
  background: #F6F9FC;
}

#sidebar-menu>ul>li.active>a svg {
  fill: #0000FF;
}

.nav-second-level li a:focus, .nav-second-level li a:hover, .nav-thrid-level li a:focus, .nav-thrid-level li a:hover,
.enlarged .left-side-menu #sidebar-menu>ul>li:hover>a,
.enlarged .left-side-menu #sidebar-menu>ul>li:hover>ul a:hover,
.nav-second-level .nav-second-level__item.nav-second-level__item_active a {
  color: #0000FF;
}

// Checkbox
.checkbox-custom {
  &.checkbox {
    label {
      padding-left: 0;

      &:before {
        top: 0;
        border-radius: 0;
        border: 1px solid #454F5B;
        width: 14px;
        height: 14px;
      }
    }

    input[type=checkbox]:checked+label::after {
      top: 2px;
      left: 5px;
      width: 4px;
      height: 8px;
      border-right-width: 1px;
      border-bottom-width: 1px;
      border-color: #454F5B;
    }
  }

  &.checkbox-single {
    input {
      height: 14px;
      width: 14px;
    }

    label {
      height: 14px;
      width: 14px;
    }
  }
}

// Buttons
.btn-success {
  &_custom {
    line-height: 1;
    padding: .375rem .589rem;
    background: #B6FFE8;
    border-color: #B6FFE8;
    border-radius: 0;
    font-size: 12px;
    color: #000;

    &:hover {
      color: #000;
      background: #00EBA8;
      border-color: transparent;
    }
  }

  &_dots {
    margin-left: 3px;
    background: #00EBA8;
    padding: .375rem .407rem;
  }

  &__dot {
    width: 2px;
    height: 2px;
    border-radius: 50%;
    display: block;
    background: #000;

    &:first-child {
      margin-top: 1px;
    }

    & + & {
      margin-top: 2px;
    }

    &:last-child {
      margin-bottom: 1px;
    }
  }
}

.bookmark-plus__icon {
  margin-left: 18px;
  line-height: 80px;

  @media (max-width: 1028px) {
    line-height: 70px;
  }
}

// Select
.custom-select-sm {
  width: 52px !important;
  height: 28px;
  border: 1px solid #758796;
  border-radius: 0;
  font-size: 12px;
  padding: 0 10px;
  color: #212B35;
  appearance:none;
  background-image: url('../images/dropdown-arrow.svg');
  background-repeat: no-repeat;
  outline: none;
  background-position: calc(100% - 11px) center;
  background-size: 8px 4.94px;
  margin-left: 11px;
}

// Card
.card-square {
  border-radius: 0;
  padding: 40px 42px;
  margin-bottom: 30px;
}

.card-grey {
  background: #F6F9FC;
}

.title-medium {
  font-size: 18px;
  font-weight: 600;
  margin: 0;
  line-height: 32px;
}

.btn {
  border-radius: 0;
}

.button {
  &_lg {
    font-size: 16px;
    padding: 0 12px;
    height: 34px;
  }

  &_sm {
    font-size: 12px;
    padding: 0 9px;
    height: 26px;
  }

  &_primary {
    background: #00EBA8;
    border-color: #00EBA8;
    color: #000;

    &:hover {
      background: #0000FF;
      border-color: #0000FF;
      color: #fff;
    }
  }

  &_secondary {
    background: #B6FFE8;
    border-color: #B6FFE8;
    color: #000;

    &:hover {
      background: #0000FF;
      border-color: #0000FF;
      color: #fff;
    }
  }

  &_tag {
    background: #fff;
    border-color: #00EBA8;
    color: #000;

    &:hover {
      background: #0000FF;
      border-color: #0000FF;
      color: #fff;
    }
  }
}

.contacts-card {
  padding: 35px 42px;

  &__number {
    font-size: 36px;
    line-height: 1;
    color: #212B35;

    &_danger {
      color: #ED1C24;
    }
  }

  &__title {
    font-size: 14px;
    line-height: 1;
    margin-top: 10px;
  }

  &__text {
    font-size: 12px;
    line-height: 1.4;
    color: #454F5B;
    margin-top: 16px;

    span {
      color: #212B35;
    }
  }

  &__link {
    color: #212B35;
    display: inline-block;
    position: relative;
    z-index: 1;

    &:hover {
      color: #00f;
    }

    &:after {
      content: '';
      position: absolute;
      display: block;
      left: 0;
      right: 0;
      bottom: 1px;
      width: 100%;
      height: 2px;
      background: #B6FFE8;
      z-index: -1;
    }
  }
}

.statistics-card {
  padding: 40px 16px 52px;

  &__knobs {
    margin-top: 38px;
  }
}

.cust-card-wrap {
  &__buttons {
    font-size: 0;
    margin: 0 -10px;

    &:not(&_aside) {
      .btn {
        margin-top: 15px;
      }
    }

    .btn {
      margin: 0 10px;
    }

    &_aside {
      white-space: nowrap;
    }
  }
}

.fw-600 {
  font-weight: 600;
}

.pages-link {
  color: #212b35;
  position: relative;
  display: inline-block;
  z-index: 1;

  &::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 1px;
    left: 0;
    right: 0;
    width: 100%;
    height: 3px;
    background: #B6FFE8;
    z-index: -1;
  }

  &:hover {
    color: #3827c1;
  }
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6{
  font-family: "Proxima Nova,sans-serif";
}