.knob-wrap {
  display: block;
  position: relative;
  width: 50px;
  height: 50px;
  background: #fff;
  border-radius: 50%;

  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    background-image: url("../images/knob-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &_down:after {
    transform: translate(-50%, -50%) rotate(180deg);
  }
}

.knob-text {
  margin-left: 20px;
  font-size: 12px;

  p {
    margin: 2px 0 10px;
    color: #212B35;
  }

  span {
    color: #454F5B;
  }
}