@media (min-width: 1029px) {
  .nav-second-level {
    height: auto !important;
    width: 168px;
    background: #F6F9FC;
    bottom: 0;
    position: fixed !important;
    top: 80px;
    left: 100px;
    text-align: left;
    list-style: none;
    display: flex !important;
    flex-direction: column;
    padding: 0 !important;
    transition: opacity .2s !important;

    &__item {
      display: flex;
      align-items: center;
      width: 100%;
      line-height: 40px;
      padding: 0 30px;
      box-sizing: border-box;

      &_active {
        background: #ECF1F8;
        pointer-events: none;

        .nav-second-level__link {
          color: #0000FF;
        }
      }
    }

    &__link {
      color: #212B35;
      font-size: 14px;
      transition: all .4s;
      padding: 0 !important;
    }
  }

  .metismenu .collapse {
    visibility: hidden;
    opacity: 0;
  }

  .metismenu .collapse.in {
    visibility: visible;
    opacity: 1;
  }

  .metismenu .collapse.in {
    z-index: 1;
  }

  .metismenu > li.active > a {
    pointer-events: none;
  }
}

@media (max-width: 1028px) {
  .breadcrumb-item {
    font-size: 17px;
  }

  .logo-box .logo svg {
    width: 30px;
  }

  .navbar-custom, .navbar-custom .app-search {
    height: 70px;
  }

  .left-side-menu {
    top: 70px;
  }

  .topnav-menu-left .breadcrumb-item, .logo {
    line-height: 70px;
  }

  .navbar-custom .topnav-menu .create-btn.nav-link {
    margin: 11.5px 10px;
  }

  .navbar-custom .topnav-menu .nav-link {
    line-height: 70px;
    max-height: 70px;
  }

  .user-profile-figure {
    margin: 11.5px 0;
  }

  .notification-list .noti-icon-badge {
    top: 17px;
  }

  #sidebar-menu>ul>li>a {
    font-size: 14px;
  }

  .enlarged .left-side-menu #sidebar-menu>ul>li:hover>ul.collapse.in {
    display: block !important;
  }
}

@media (max-width: 767.98px) {
  .left-side-menu {
    display: block;
  }

  .logo-box {
    display: block;
  }

  .content-page, .enlarged .content-page {
    margin-left: 70px!important;
  }

  .nav-bordered a.active {
    border-width: 2px !important;
  }

  .cust-card-wrap .btn {
    margin-top: 10px;
  }

  .card-square {
    padding: 20px 22px;
    margin-bottom: 20px;
  }

  .button_lg {
    font-size: 14px;
    padding: 0 10px;
    height: 30px;
  }

  .contacts-card__number {
    font-size: 30px;
  }

  .DTFC_LeftWrapper,
  .DTFC_RightWrapper {
    display: none;
  }

  .diriq-table td:nth-child(2):after, .diriq-table th:nth-child(2):after {
    display: none;
  }

  table.dataTable tbody td, table.dataTable tbody th {
    padding-top: 8px!important;
    padding-bottom: 8px!important;
  }

  .diriq-table td, .diriq-table th {
    padding-left: 10px!important;
    padding-right: 10px!important;
    min-width: 50px;
  }

  .diriq-table td:first-child, .diriq-table th:first-child {
    padding-left: 12px!important;
  }

  .diriq-table td:nth-child(2), .diriq-table th:nth-child(2) {
    width: 150px!important;
    padding-left: 15px!important;
    padding-right: 15px!important;
  }

  .diriq-table__email {
    position: relative;
    z-index: 1;
    &:after {
      z-index: -1;
      content: '';
      position: absolute;
      display: block;
      left: 0;
      right: 0;
      bottom: 4px;
      width: 100%;
      height: 2px;
      background: #b6ffe8;
    }
  }

  .diriq-table__wrapper {
    padding-right: 0;
  }

  .diriq-table__filter-btn {
    width: 38px;
    height: 38px;
    right: 0;
  }

  .dataTables_wrapper .dataTables_paginate {
    margin-top: 0 !important;
  }

  div.dataTables_wrapper div.dataTables_paginate {
    margin-left: 11px;
  }

  .nav-tabs .nav-link, .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
    font-size: 13px;
  }

  .diriq-table__bottom {
    margin-top: 8px;
  }

  .diriq-table__more-btn {
    right: -16px;
    width: 16px;

    &-circle, &-circle:before, &-circle:after {
      width: 4px;
      height: 4px;
    }
  }

  .statistics-card__inner {
    width: 100%;
  }

  .cust-card-wrap__buttons_aside {
    margin-top: 20px;
  }
}

@media (min-width: 577px) {
  .hamburger {
    display: none;
  }
}

@media (max-width: 576px) {
  .nav-bordered {
    display: flex;
    justify-content: space-between;

    li a {
      padding: 6px 4px 4px!important;
    }
  }

  .tab-content {
    padding-top: 12px;
  }

  .table td {
    padding: 20px 6px;
  }

  .table th {
    padding: .7rem 6px;
  }

  .table td, .table th {
    &:first-child {
      text-align: center;
    }
  }

  .home-page .main-table-wrap #main-datatable {
    min-width: 788px;
  }

  .content-page, .enlarged .content-page {
    margin-left: 0!important;
    padding: 0 6px;
  }

  .nav-tabs .nav-link, .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
    font-size: 12px;
  }

  .statistics-card__knobs {
    flex-direction: column;
    margin-top: 16px;
  }

  .statistics-card__knobs-item {
    & + & {
      margin-top: 18px;
    }
  }

  .statistics-card__inner {
    width: auto;
  }
}

@media (max-width: 320px) {
  .nav-tabs .nav-link, .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
    font-size: 11px;
  }
}