.chart-title {
  font-size: 18px;
  font-weight: 600;
  margin: 0 0 1rem 0;
  white-space: nowrap;
}

.chart-legend {
  font-size: 12px;
  color: #454F5B;
  text-align: center;

  span {
    white-space: nowrap;
  }
}

.card-chart {
  padding: 37px;
}

.morris-chart {
  color: #454F5B;
}

.morris-hover.morris-default-style {
  background: #ecf1f8;
  box-shadow: 0 0 35px 0 rgba(154,161,171,.15);
}

.morris-hover-row-label {
  color: #454F5B;
}

.circle-i {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #000;
  border-radius: 50%;
  margin-right: 3px;
  vertical-align: middle;

  &_openers {
    background: #00EBA8;
  }

  &_clickers {
    background: #0000FF;
  }

  &_unsubscribers {
    background: #ED1C24;
  }
}