th, td {
  white-space: nowrap !important;
}

table {
  table-layout: fixed !important;
}

.list-quality-icon {
  display: inline-block;
  white-space: nowrap;
  font-size: 0;
  &__circle {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #DAE8F7;

    & + & {
      margin-left: 2px;
    }
  }
  &_2 &__circle{
    &:first-child, &:nth-child(2) {
      background: #B6FFE8;
    }
  }
  &_3 &__circle{
    &:first-child, &:nth-child(2), &:nth-child(3) {
      background: #00EBA8;
    }
  }
}

table.dataTable tbody th, table.dataTable tbody td {
  padding-top: 12px !important;
  padding-bottom: 12px !important;
}


.diriq-table {
  color: #212B35;
  font-size: 12px;

  th {
    &:focus {
      outline: none;
    }
  }

  thead th {
    background: #DAE8F7;

    &:nth-child(2) {
      ._list-name-table &:after {
        background: #fff;
      }
    }
  }

  tfoot th {
    background: #F6F9FC;
  }

  th, td {
    font-weight: 400 !important;
    padding-left: 12px !important;
    padding-right: 12px !important;


    ._list-name-table & {
      min-width: 90px;
      padding-left: 27px !important;
      padding-right: 13px !important;
    }

    &:first-child {
      text-align: center;
      padding-left: 29px !important;
      padding-right: 10px !important;
      min-width: auto;
      width: 15px;
    }

    ._list-name-table &:nth-child(2) {
      width: 174px !important;
      padding-left: 18px !important;
      padding-right: 28px !important;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        height: 100%;
        width: 1px;
        background: #DAE8F7;
      }
    }

    ._my-lists-table &:nth-child(2) {
      width: 180px !important;
    }

    ._my-lists-table &:nth-child(3),
    ._my-lists-table &:nth-child(4),
    ._my-lists-table &:nth-child(5),
    ._my-lists-table &:nth-child(6),
    ._my-lists-table &:nth-child(7) {
      text-align: right;
    }

    input {
      width: 100%;
    }

    &.nofilter {
      input {
        opacity: 0;
        visibility: hidden;
      }
    }
  }

  tbody tr:first-child td {
    border-top: none;

    ._filter-visible & {
      border-top: 38px solid #f6f9fc;
    }
  }

  tbody tr {
    height: 50px !important;
  }

  &__wrapper {
    padding-right: 20px;
    position: relative;

    label {
      margin-bottom: 0;
      font-weight: 400;
    }
  }

  &__btn-cell {
    font-size: 0;
  }

  &__email {
    color: #212B35;
    display: inline-block;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;

    &:hover {
      color: #00f;
    }

    .DTFC_LeftBodyLiner &,
    .diriq-table__wrapper:not(._list-name-table) & {
      position: relative;
      z-index: 1;

      &:after {
        z-index: -1;
        content: '';
        position: absolute;
        display: block;
        left: 0;
        right: 0;
        bottom: 4px;
        width: 100%;
        height: 2px;
        background: #B6FFE8;
      }
    }
  }

  &__bottom {
    margin-top: 12px;
  }

  &__list-size-number {
    font-size: 24px;
    line-height: 1;
    margin-bottom: 4px;
  }

  &__list-size-percents {
    font-size: 12px;
    line-height: 1;
  }

  &__cell-title {
    font-size: 12px;
    font-weight: 600;
    color: #212B35;
    display: inline-block;
    position: relative;
    z-index: 1;

    &::after {
      content: "";
      display: block;
      position: absolute;
      bottom: 2px;
      left: 0;
      right: 0;
      width: 100%;
      height: 4px;
      background: #B6FFE8;
      z-index: -1;

    }

  }

  .checkbox-custom {
    margin-top: 4px;
  }

  &.dataTable thead th.sorting,
  &.dataTable thead th.sorting_asc,
  &.dataTable thead th.sorting_desc {
    background-image: none;
  }

  .sort-i {
    margin-left: 12px;
  }

  &__filter-btn {
    width: 60px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    position: absolute;
    top: 0;
    right: 20px;
    cursor: pointer;
  }
  &__more-btn {
    width: 20px;
    height: 38px;
    background: #00EBA8;
    position: absolute;
    right: 0;
    top:0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: 0.4s;

    &:hover {
      background: #0000FF;
    }

    &:hover &-circle, &:hover &-circle:before, &:hover &-circle:after {
      background: #fff;
    }

    &-circle {
      display: block;
      width: 5px;
      height: 5px;
      background: #000;
      position: relative;
      border-radius: 50%;
      transition: 0.4s;

      &:after, &:before {
        content: '';
        position: absolute;
        left: 0;
        display: block;
        width: 5px;
        height: 5px;
        background: #000;
        border-radius: 50%;
        transition: 0.4s;
      }

      &:after {
        top: -10px;
      }

      &:before {
        bottom: -10px;
      }
    }
  }

  &__filter-icon,
  &__close-icon {
    color: #00EBA8;
    transition: color 0.25s;
  }

  &__filter-btn:hover &__filter-icon,
  &__filter-btn:hover &__close-icon {
    color: #0000FF;
  }

  &__close-icon-cross {
    fill: #000;
    transition: fill 0.25s;
  }

  &__filter-btn:hover &__close-icon-cross {
    fill: #fff;
  }

  body:not(._filter-visible) &__close-icon,
  body._filter-visible &__filter-icon {
    display: none;
  }
}

.dataTables_wrapper.no-footer .dataTables_scrollBody,
table.dataTable thead th, table.dataTable thead td {
  border: none !important;
  border-bottom: 0 solid #F6F9FC !important;
}

.dataTables_scrollFoot,
.DTFC_LeftFootWrapper,
.DTFC_RightFootWrapper {
  position: absolute !important;
  top: 38px !important;
  left: 0 !important;
}

.dataTables_wrapper .dataTables_paginate li.paginate_button {
  display: none;
  border: none !important;
  &.previous, &.next {
    display: block;

    .page-link {
      display: block;
      width: 24px;
      height: 24px;
      font-size: 0;
      border: none;
      border-radius: 0;
      background: #B6FFE8;
      position: relative;
      color: #000;
      transition: 0.4s;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url('../images/dropdown-arrow.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 8px 4.94px;
      }

      &:hover {
        background: #00eba8;
        color: #fff;
      }
    }
  }

  &.previous {
    .page-link:before {
      transform: rotate(90deg);
    }
  }

  &.next {
    margin-left: 12px;

    .page-link:before {
      transform: rotate(-90deg);
    }
  }

  &.disabled {
    opacity: 0.5;
  }
}

div.dataTables_wrapper {
  div.dataTables_info {
    padding-top: 0;
  }

  div.dataTables_paginate ul.pagination {
    margin: 0 !important;
    font-size: 0 !important;
  }

  div.dataTables_paginate {
    margin-left: 18px;
    display: flex;
    align-items: center;
    float: none;
    padding-top: 0;
    text-align: left;
  }

  div.dataTables_info {
    font-size: 12px;
    color: #212B35;
    line-height: 28px;
  }
}

.sort-i {
  .sorting_asc &__top,
  .sorting_desc &__bottom {
    fill: #0000FF;
  }
}

table.dataTable tfoot th, table.dataTable tfoot td {
  border: none !important;
}

table.dataTable tfoot th, table.dataTable tfoot td {
  padding-top: 9px !important;
  padding-bottom: 9px !important;
}

._my-lists-table table.dataTable tbody td,
._my-lists-table table.dataTable tbody th {
  padding-top: 28px!important;
  padding-bottom: 22px!important;
  padding-left: 20px!important;
  padding-right: 20px!important;
}

.dataTables_scrollBody tfoot {
  visibility: collapse;
}

.DTFC_LeftFootWrapper th:nth-child(2) {
  &:after {
    display: none;
  }
}

.DTFC_RightBodyLiner th,
.DTFC_RightBodyLiner td,
.DTFC_RightHeadWrapper th,
.DTFC_RightFootWrapper th {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 6px;
    background: rgba(#000, 0.16);
  }
}

.diriq-table__wrapper .DTFC_RightBodyLiner td {
  padding-left: 22px !important;
}

body:not(._filter-visible) .dataTables_scrollFoot,
body:not(._filter-visible) .DTFC_LeftFootWrapper,
body:not(._filter-visible) .DTFC_RightFootWrapper {
  display: none;
}

.DTFC_ScrollWrapper,
.DTFC_LeftBodyWrapper,
.DTFC_RightBodyWrapper,
.DTFC_LeftBodyLiner,
.DTFC_RightBodyLiner {
  height: auto !important;
  max-height: none !important;
}

.table-status {
  padding: 2px 5px;
  border: 1px solid #212b35;
  font-weight: 600;

  &_success {
    color: #00EBA8;
    border-color: #00EBA8;
  }

  &_danger {
    color: #ED1C24;
    border-color: #FFCFEF;
  }
}

.table-tag {
  padding: 2px 5px;
  background: #ECF1F8;
  & + & {
    margin-left: 8px;
  }
}

.filter-input {
  border: none;
  outline: none;
  line-height: 20px;
  font-size: 10px;
  padding: 0 7px;

  &::placeholder {
    color: #758796;
  }
}

.DTFC_RightHeadWrapper,
.DTFC_RightBodyWrapper {
  overflow: hidden;
}

.nosort .sort-i {
  display: none;
}