.navbar-custom {
  height: 80px;
  padding: 0 20px 0 0;

  .breadcrumb-item {
    line-height: 80px;
    padding-left: .5rem;

    &:before {
      content: '/';
      font-family: inherit;
      display: inline-block;
      padding-right: 12px;
      color: #00EBA8;
      font-weight: 700;
    }

    &:first-child {
      padding-left: 0;
    }
  }

  .app-search {
    max-width: none;
    transition: max-width 0.4s;
    margin-right: 9px;
    height: 80px;

    .input-group-append .btn {
      border-left: none;
      transition: 0.4s;
    }
  }

  .app-search .form-control,
  &-light .app-search .btn {
    height: 34px;
  }

  &-light {
    .app-search .form-control,
    .app-search .btn {
      background-color: #F6F9FC;
      border-color: #F6F9FC;
      color: #0000FF;
      padding-top: 0;
      padding-bottom: 0;

      .fe-search {
        font-size: 22px;
      }
    }

    .app-search .form-control {
      transition: 0.4s;
      width: 70px !important;
      color: #212B35;
      border: 1px solid #F6F9FC;
      border-right: none;
      font-size: 12px;

      &::placeholder {
        color: transparent !important;
        transition: color 0.4s;
      }

      &:hover, &:focus {
        width: 213px !important;

        &::placeholder {
          color: #758796 !important;
        }
      }

      &:focus {
        &, & + .input-group-append .btn {
          border-color: #0000FF;
          background: #fff;
        }
      }
    }
  }

  .topnav-menu {
    .create-btn.nav-link {
      border: 1px solid #00f;
      border-radius: 23px;
      max-height: 47px;
      line-height: 45px;
      padding: 0 18px;
      color: #000;
      font-size: 18px;
      margin: 16.5px 10px;

      i {
        margin-left: 7px;
      }
    }

    .nav-link {
      line-height: 80px;
      max-height: 80px;
      padding: 0 10px;
    }
  }
}

.nav-pills>li>a, .nav-tabs>li>a {
  color: #212B35;
}

.breadcrumb-item {
  font-size: 22px;
  font-weight: 500;

  &.active, &>a {
    color: #0000FF;
  }

  a {
    margin-right: 6px;
  }
}

.logo {
  line-height: 80px;
}

.notification-list .noti-icon-badge {
  top: 23px;
  right: 5px;
}

.badge-danger {
  background-color: #ED1C24;
}

.user-profile-figure {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  box-sizing: border-box;
  border: 1px solid #0000FF;
  color: #000;
  font-size: 18px;
  margin: 17.5px 0;
}

.user-profile-link {
  position: relative;
  padding: 0 22px 0 10px !important;

  i {
    position: absolute;
    top: 0;
    right: 10px;
  }
}